import React from 'react';
import Main from './panels/Main';
import connect from '@vkontakte/vk-connect';
import { Snackbar, Avatar } from '@vkontakte/vkui';
import { Icon16Done } from '@vkontakte/icons';
import copy from 'copy-to-clipboard';

var crypto = require('crypto');



const APIConf = {
    version: 'v1',
    url: 'https://xcoin-backend.herokuapp.com',  // :port
}
const APIUrl = `${APIConf.url}/api/${APIConf.version}`;



class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
            fetchedUser: null,
			userAcc: null,
            usersTop: [],
            xc_balance: 0,
            clicks: 0,
            reputation: 0,
            speed: 0,  // при авто майнинге
            click_price: 0,
            activeModal: null,
            modalHistory: [],
            snackbar: null,
            transfer_xc_amount: 0.0001,
            transfer_currency: null
		}
	}
    
    setActiveModal = (activeModal) => {
        activeModal = activeModal || null;
        let modalHistory = this.state.modalHistory ? [...this.state.modalHistory] : [];
    
        if (activeModal === null) {
          modalHistory = [];
        } else if (modalHistory.indexOf(activeModal) !== -1) {
          modalHistory = modalHistory.splice(0, modalHistory.indexOf(activeModal) + 1);
        } else {
          modalHistory.push(activeModal);
        }
        
        this.setState({ snackbar: null});
        this.setState({
          activeModal,
          modalHistory
        });
    };


    openSnackBarCopied = (data) => {
        if (this.state.snackbar) return;
        copy(data);

        this.setState({activeModal: null});
        this.setState({ snackbar:
          <Snackbar
            onClose={() => this.setState({ snackbar: null })}
            // action="Поделиться"
            // onActionClick={() => this.setState({ text: 'Добавляем метку.' })}
            before={<Avatar size={24} style={{ background: 'var(--accent)' }}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
          >
            Адрес кошелька скопирован
          </Snackbar>
        });
      }


	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
                    console.log(e.detail.data);
                    this.setState({ fetchedUser: e.detail.data });
                    this.createUser(e.detail.data);
                    this.getUsersTop();
                    // this.getUpgradesOne()
                    // this.getUpgradesTwo()
					break;
					case 'VKWebAppShowWallPostBoxResult':

  					break;
				default:
				
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
        setInterval(() => {
            // this.autoMine();
            this.getUsersTop();
        }, 25000)
	}


    generateToken = (data_string) => {
        let date = Date.now();
        let ready_string = `${data_string}${date}`.split("").reverse().join("");
        return [date, crypto.createHash('sha256').update(ready_string).digest('hex')];
    }

	
    createUser = (data) => {
        let tokenAndDate = this.generateToken(data.id + data.first_name + data.last_name + data.photo_100);
        fetch(`${APIUrl}/user/create`, {
            method: 'post',
            headers: {
                "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
            },
            body: `token=${tokenAndDate[1]}&time=${tokenAndDate[0]}&user_vk=${data.id}&first_name=${data.first_name}&last_name=${data.last_name}&photo_100=${data.photo_100}`
        })
            .then((response) => response.json())
            .then((response) => {
                this.setState({ userAcc: response })
                this.setState({ xc_balance: response.xc_balance, clicks: response.clicks, reputation: response.reputation, speed: response.speed, click_price: response.click_price })
            })
            .catch((error) => {
                console.log(`Братан, здесь ошибка! - ${error}`);
            })
    }


    getUsersTop = () => {
        let tokenAndDate = this.generateToken('');
        fetch(`${APIUrl}/user/get_top_users`, {
            method: 'post',
            headers: {
                "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
            },
            body: `time=${tokenAndDate[0]}&token=${tokenAndDate[1]}`
        })
            .then((response) => response.json())
            .then((response) => {
                this.setState({ usersTop: response.users })
            })
            .catch((error) => {
                console.log(`Братан, здесь ошибка! - ${error}`);
            })
    }


    updateBalanceByClick = (user_vk) => {
        let tokenAndDate = this.generateToken(user_vk);
        fetch(`${APIUrl}/user/balance/update_by_click`, {
            method: 'post',
            headers: {
                "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
            },
            body: `time=${tokenAndDate[0]}&token=${tokenAndDate[1]}&user_vk=${user_vk}`
        })
            .then((response) => response.json())
            .then((response) => {
                this.setState({ userAcc: response })
                this.setState({ xc_balance: response.xc_balance, clicks: response.clicks, reputation: response.reputation, speed: response.speed, click_price: response.click_price })
            })
            .catch((error) => {
                console.log(`Братан, здесь ошибка! - ${error}`);
            })
    }


	clickMiningBtn = () => {
        setTimeout(()=>{
            this.updateBalanceByClick(this.state.fetchedUser.id)
        }, 100)
	}


    changeTransferXC = (num) => {
        this.setState({transfer_xc_amount: num});
    }


	render() {

		return(
			<Main transfer_currency={this.state.transfer_currency} setState={this.setState} changeTransferXC={this.changeTransferXC} transfer_xc_amount={this.state.transfer_xc_amount} snackBar={this.state.snackbar} openSnackBarCopied={this.openSnackBarCopied} activeModal={this.state.activeModal} setActiveModal={this.setActiveModal} xc_balance={this.state.xc_balance} click_price={this.state.click_price} userAcc={this.state.userAcc} clickMiningBtn={this.clickMiningBtn} usersTop={this.state.usersTop} />
		);
	}
}


export default App;
