import React from 'react';


import { Button, ModalRoot, ModalCard, ViewWidth, ConfigProvider, AdaptivityProvider, View, SplitLayout, PanelHeader, SplitCol, Panel, Group, Title, Epic, Tabbar, TabbarItem, PanelHeaderBack, Placeholder, useAdaptivity, AppRoot } from '@vkontakte/vkui';
import { Icon24Add, Icon56MoneyTransferOutline, Icon28BankOutline, Icon28WalletOutline, Icon28ComputerOutline, Icon28ShoppingCartOutline, Icon28UserCircleOutline} from '@vkontakte/icons';
import '@vkontakte/vkui/dist/vkui.css';

import Mining from './Mining';
import Shop from './Shop';
import Profile from './Profile';
import Payments from './Payments';



const Main = ({transfer_currency, setState, changeTransferXC, transfer_xc_amount, snackBar, openSnackBarCopied, activeModal, setActiveModal, xc_balance, click_price, userAcc, clickMiningBtn, usersTop}) => {
    const { viewWidth } = useAdaptivity();
    const [activeStory, setActiveStory] = React.useState('mining');
    const onStoryChange = (e) => setActiveStory(e.currentTarget.dataset.story);

    const modal = (
        <ModalRoot activeModal={activeModal}>
            <ModalCard
                id="wallet_address"
                onClose={() => setActiveModal(null)}
                icon={<img src={"https://api.qrserver.com/v1/create-qr-code/?size=150x150&data=" + (userAcc ? userAcc.xc_address : "QR Code не загрузился")}></img>}
                header={userAcc ? userAcc.xc_address : "Адрес кошелька еще не загрузился.."}
                subheader="Вы можете его скопировать и отправить кому-нибудь, чтобы вам перевели несколько коинов"
                actions={
                    <Button mode="secondary" size="l" before={<Icon24Add/>} onClick={() => openSnackBarCopied(
                        userAcc ? userAcc.xc_address : null
                    )}>
                    Скопировать
                    </Button>
                }
            >
            </ModalCard>
        </ModalRoot>
    );
	
    return(
        <ConfigProvider>
            <AdaptivityProvider>
                <AppRoot>
                    <SplitLayout header={<PanelHeader separator={false} />} style={{ justifyContent: "center" }}>
                        <SplitCol animate={true} spaced={viewWidth > ViewWidth.MOBILE} width={'560px'} maxWidth={'560px'}>
                        <Epic activeStory={activeStory} tabbar={
                                <Tabbar>
                                <TabbarItem
                                    onClick={onStoryChange}
                                    selected={activeStory === 'stock'}
                                    data-story="stock"
                                    text="Биржа"
                                ><Icon28BankOutline /></TabbarItem>
                                <TabbarItem
                                    onClick={onStoryChange}
                                    selected={activeStory === 'payments'}
                                    data-story="payments"
                                    text="Переводы"
                                ><Icon28WalletOutline/></TabbarItem>
                                <TabbarItem
                                    onClick={onStoryChange}
                                    selected={activeStory === 'mining'}
                                    data-story="mining"
                                    // label="12"
                                    text="Майнинг"
                                ><Icon28ComputerOutline /></TabbarItem>
                                <TabbarItem
                                    onClick={onStoryChange}
                                    selected={activeStory === 'shop'}
                                    data-story="shop"
                                    text="Магазин"
                                ><Icon28ShoppingCartOutline /></TabbarItem>
                                <TabbarItem
                                    onClick={onStoryChange}
                                    selected={activeStory === 'profile'}
                                    data-story="profile"
                                    text="Профиль"
                                ><Icon28UserCircleOutline /></TabbarItem>
                                </Tabbar>
                            }>
                            <View id="stock" activePanel="stock">
                            <Panel id="stock">
                                <PanelHeader left={<PanelHeaderBack />}>Новости</PanelHeader>
                                <Group style={{ height: '1000px' }}>
                                <Placeholder icon={<Icon28BankOutline width={56} height={56} />} />
                                </Group>
                            </Panel>
                            </View>
                            <View id="payments" activePanel="payments">
                            <Panel id="payments">
                                <PanelHeader left={<Icon28WalletOutline />}><Title level='1' weight="semibold">Переводы</Title></PanelHeader>
                                <Payments transfer_currency={transfer_currency} setState={setState} changeTransferXC={changeTransferXC} transfer_xc_am={transfer_xc_amount} xc_balance={xc_balance} />
                            </Panel>
                            </View>
                            <View id="mining" activePanel="mining">
                            <Panel id="mining">
                                <PanelHeader left={<Icon28ComputerOutline />}><Title level='1' weight="semibold">Майнинг</Title></PanelHeader>
                                <Mining balanceXC={xc_balance} clickPriceXC={click_price} clickMiningBtn={clickMiningBtn} usersTop={usersTop} />
                            </Panel>
                            </View>
                            <View id="shop" activePanel="shop">
                            <Panel id="shop">
                                <PanelHeader left={<Icon28ShoppingCartOutline />}><Title level='1' weight="semibold">Магазин</Title></PanelHeader>
                                <Shop />
                            </Panel>
                            </View>
                            <View id="profile" activePanel="profile" modal={modal}>
                            <Panel id="profile">
                                <PanelHeader left={<Icon28UserCircleOutline />}><Title level='1' weight="semibold">Профиль</Title></PanelHeader>
                                <Profile userAcc={userAcc} setActiveModal={setActiveModal}/>
                            </Panel>
                            </View>
                        </Epic>
                        </SplitCol>
                        {snackBar}
                    </SplitLayout>
                </AppRoot>
            </AdaptivityProvider>
        </ConfigProvider>
    );
}

export default Main;