import React from 'react';
import { Div, Group, Title, Card, Header, Button, Spacing, Headline, Subhead, RichCell, Avatar, CardGrid} from '@vkontakte/vkui';
import {Icon24Add} from '@vkontakte/icons';
import '../css/dist/style.css';

const Mining = ({balanceXC, clickPriceXC, clickMiningBtn, usersTop}) => {
    return (
        <Group>
            <CardGrid>
                <Card size="l" mode="shadow" style={{width: '100%'}}>
                    <Group>
                        <Header subtitle='Ваш текущий баланс XCoin'>Баланс</Header>
                        <Title className='balance-label' level='1' weight='regular'>{parseFloat(balanceXC).toFixed(8)} <span>XC</span></Title>
                    </Group>
                </Card>
            </CardGrid>
            <Spacing size={24} />
            <Group>
                <Div>
                    <Headline weight='semibold'>Кликать</Headline>
                    <Subhead weight='regular' style={{color: '#818C99'}}>Стоимость вашего клика - {parseFloat(clickPriceXC).toFixed(8)} XC</Subhead>
                    <Spacing size={13} />
                    <Button mode="commerce" size="m" before={<Icon24Add/>} style={{width: '100%'}} onClick={clickMiningBtn}>Нажми</Button>
                </Div>
            </Group>
            <Spacing size={13} />
            <Group>
                <Div>
                    <Headline weight='semibold'>Рейтинг</Headline>
                    <Subhead weight='regular' style={{color: '#818C99'}}>Топ пользователей</Subhead>
                    <Spacing size={13} />
                    {
                        usersTop ? usersTop.map(user => (
                            <RichCell onClick={() => window.open(`https://vk.com/id${user.user_vk}`)} 
                            key={user.id} before={<Avatar size={48} src={user.photo_100} />} caption={parseFloat(user.xc_balance).toFixed(8) + ' XC'} after={user.id}>
                                {`${user.first_name} ${user.last_name}`}
                            </RichCell>
                        )) : <h1>Рейтинг не загрузился!</h1>
                    }
                </Div>
            </Group>
        </Group>
    );
};

export default Mining;