import React from 'react';
import { Group, Header, FormLayout, FormLayoutGroup, FormItem, Select, Input, Slider, CustomSelectOption} from '@vkontakte/vkui';
import { Icon24Coins } from '@vkontakte/icons';
import '../css/dist/style.css';

const Payments = ({changeTransferXC, transfer_xc_am, xc_balance}) => {
    function checkAmount(isStatus) {
        if (transfer_xc_am < 0.0001) {
            if (isStatus) {
                return 'error';
            } else {
                return 'Минимальная сумма перевода другим пользователям 0.0001 XC!';
            }
        } else {
            if (isStatus && transfer_xc_am > xc_balance) {
                return 'error'
            } else {
                if (!isStatus && transfer_xc_am > xc_balance) return 'Данное значение больше вашего баланса!';
            }
        }
    }

    return (
        <Group>
            <Header mode="secondary">Переводы по адресу кошелька</Header>
            <FormLayout>
                <FormLayoutGroup mode="horizontal">
                    <FormItem>
                        <Select placeholder="Выберите криптовалюту"
                        options={[{
                            value: 'xCoin', label: 'xCoin'
                        }]} onChange={this.options.value = 'xCoin'}/>
                    </FormItem>

                    <FormItem>
                        <Input placeholder="Введите адрес кошелька"/>
                    </FormItem>
                </FormLayoutGroup>
                
                <FormLayoutGroup mode="horizontal">
                    <FormItem top={`Процент от вашего баланса: ${Math.ceil(parseFloat(100 / (xc_balance / Number(transfer_xc_am))))}%`}>
                        <Slider min={0.0001} step={0.0001} max={parseFloat(xc_balance).toFixed(8)}
                            value={Number(transfer_xc_am)}
                            onChange={transfer_xc_amount => changeTransferXC(transfer_xc_amount)}
                        />
                    </FormItem>

                    <FormItem top={'Ваш баланс xCoin: ' + parseFloat(xc_balance).toFixed(8)}
                    status={checkAmount(true)}
                    bottom={checkAmount(false)}
                    >
                        <Input value={String(transfer_xc_am)} onChange={e => changeTransferXC(e.target.value)} type="number" />
                    </FormItem>
                </FormLayoutGroup>
            </FormLayout>
        </Group>
    );
};

export default Payments;