import React from 'react';
import { Group, Title, Button, Avatar, Gradient, Text, SimpleCell, CellButton, Header, Caption} from '@vkontakte/vkui';
import {Icon28SchoolOutline, Icon28AddOutline, Icon28QrCodeOutline} from '@vkontakte/icons';
import '../css/dist/style.css';

const Profile = ({ setActiveModal, userAcc}) => {

    return (
        <Group>
            <Gradient style={{
            margin: '-7px -7px 0 -7px',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            textAlign: 'center',
            padding: 32,
          }}>
            <Avatar size={96} src={userAcc ? userAcc.photo_100 : null}/>
            <Title style={{ marginBottom: 8, marginTop: 20 }} level="2" weight="medium">
                {userAcc ? userAcc.first_name : 'Имя'} {userAcc ? userAcc.last_name : 'Фамилия'}
            </Title>
            <Text style={{ color: 'var(--text_secondary)' }}>Общий баланс аккаунта — {userAcc ? parseFloat(userAcc.xc_balance).toFixed(8) : 0} XC</Text>
            {/* <Button size="m" mode="secondary">Редактировать</Button> */}
          </Gradient>
          <Group mode="plain">
            <Header mode="secondary">Крипто-кошельки</Header>
            <SimpleCell onClick={() => setActiveModal('wallet_address')} before={<Icon28QrCodeOutline />} description="Самая первая криптовалюта" 
                indicator={<Text weight="medium">{userAcc ? parseFloat(userAcc.xc_balance).toFixed(8) : 0}</Text>}>xCoin</SimpleCell>
            <CellButton before={<Icon28AddOutline />}>Добавить криптовалюту</CellButton>
          </Group>
        </Group>
    );
};

export default Profile;